const upload = (<HTMLElement | null>document.getElementById('upload'));
const content = (<HTMLInputElement>document.getElementById('content'));

const readFile = (event: any | HTMLInputElement): void => {
  const file = event.target.files[0];
  const fReader = new FileReader();

  content.value = 'Hello';
  fReader.onload = (event: any | HTMLInputElement): void => {
    content.value = event.target.result;
  };
  fReader.readAsText(file);
}

upload && (upload.onchange = readFile);
